html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

.redmonk {
    background-color: #092324;
    right: 0;
}

.logoRM {
    height: 60px;
    padding-right: 30px;
    padding-bottom: 10px;
}

.textRM {
    font-size: 35px;
    color: antiquewhite;
    padding-bottom: 20px;
}

.title-header {
    height: 30px;
    content: attr(title);
    display: flex;
    align-items: center;
    padding-left: 20px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 18px;
    color: #fafafa;
    background-color: #1F2012;
    border-top: 1px;
    border-color: #200a01;
}

.types {
    position: fixed;
    top: 5%;
    bottom: 0;
    left: 0;
    right: 0;
    width: 10%;
    background-color: #092324;
    border-width: 1px;
    border-top: 0;
    border-color: #200a01;
    border-style: solid;
}

.playground {
    position: fixed;
    top: 5%;
    bottom: 0;
    left: 10%;
    width: 45%;
    background-color: #263238;
}

.result {
    position: fixed;
    top: 5%;
    right: 0;
    bottom: 0;
    width: 45%;
    overflow: hidden;
}

.code-editor {
    height: 100%;
    overflow: hidden;
    position: relative;
}

.CodeMirror {
    height: auto;
}

.react-codemirror2 {
    max-height: calc(100% - 30px);
    overflow: auto;
}

.iframe {
    width: 100%;
    height: 100%;
}

button {
    background-color: #26382b;
    width: 145%;
    height: 30px;
    margin-bottom: 7px;
    color: azure;
    font-size: 15px;
}